import React, {useState, Suspense } from 'react'
import {Routes, Route, Navigate} from 'react-router-dom'
// import Register from './Seller/pages/Register'
import MainNavigation from './shared/components/Navigation/MainNavigation'
// import Updateseller from './Seller/component/update/Forminput'
// import Login from './Seller/pages/Login'
// import Sellernewproduct from './product/pages/Sellernewproduct'
// import Selleralluploadedproduct from './product/pages/Selleralluploadedproduct'
// import Updatesellerproduct from './product/pages/Updatesellerproduct'
// import Allproduct from './product/pages/Allproduct'
// import Registercustomer from './customer/pages/Registercustomer'
// import Logincustomer from './customer/pages/Logincustomer'
// import Updatecustomer from './customer/pages/Updatecustomer'
// import Customerorderpage from './product/pages/Customerorderpage'
// import Updatecustomerorder from './product/pages/Updatecustomerorder'
// import Staticpage from './product/pages/Staticpage'
// import Sellerorderpage from './product/pages/Sellerorderpage'
import Cart from './cart/component/Cart/Cart'
import LoadingSpinner from './shared/components/UIElements/LoadingSpinner'
// import { useContext } from 'react'
// import { AuthContext } from './shared/context/auth-context'

const Register = React.lazy(()=> import('./Seller/pages/Register'))
const Updateseller = React.lazy(()=> import('./Seller/component/update/Forminput'))
const Login = React.lazy(()=> import('./Seller/pages/Login'))
const Sellernewproduct = React.lazy(()=> import('./product/pages/Sellernewproduct'))
const Selleralluploadedproduct = React.lazy(()=> import('./product/pages/Selleralluploadedproduct'))
const Updatesellerproduct = React.lazy(()=> import('./product/pages/Updatesellerproduct'))
const Allproduct = React.lazy(()=> import('./product/pages/Allproduct'))
const Registercustomer = React.lazy(()=> import('./customer/pages/Registercustomer'))
const Logincustomer = React.lazy(()=> import('./customer/pages/Logincustomer'))
const Updatecustomer = React.lazy(()=> import('./customer/pages/Updatecustomer'))
const Customerorderpage = React.lazy(()=> import('./product/pages/Customerorderpage'))
const Updatecustomerorder = React.lazy(()=> import('./product/pages/Updatecustomerorder'))
const Staticpage = React.lazy(()=> import('./product/pages/Staticpage'))
const Sellerorderpage = React.lazy(()=> import('./product/pages/Sellerorderpage'))

const App = () => {

  const[cartisshown, setcartisshown] = useState(false)
  // const auth = useContext(AuthContext)

  const showcarthandler=()=>{
    setcartisshown(true)
  }
  const hidecarthandler =()=>{
    setcartisshown(false)
  }


  

  const routes=   
  <Routes>
  <Route exact path='/' element={<Staticpage/>}/>
  <Route path='/:nid' element={<Allproduct/>}/>
  <Route path='/sellerregistration' element={<Register/>}/>
  <Route  path='/sellerlogin' element={<Login/>}/>
  <Route path='/:uid/sellerupdate' element={<Updateseller/>}/>  
  <Route path='/sellernewproduct' element={<Sellernewproduct/>}/>  
  <Route path='/:uid/selleralluploadedproduct' element={<Selleralluploadedproduct/>}/> 
  <Route path='/:pid/updatesellerproduct' element={<Updatesellerproduct/>}/> 
  <Route path='*' element={<Navigate to= '/'/>}/> 

  {/* customers */}  
  <Route path='/customerregistration' element={<Registercustomer/>}/>
  <Route  path='/customerlogin' element={<Logincustomer/>}/>  
  <Route path='/:cid/updatecustomer' element={<Updatecustomer/>}/> 
  <Route path='/:cid/customerorder' element={<Customerorderpage/>}/> 

  <Route path='/:uid/productcreator' element={<Sellerorderpage/>}/> 
    
  <Route path='/:oid/updatecustomerorder' element={<Updatecustomerorder/>}/> 
  </Routes>


  return (
    <>
    
    {cartisshown && <Cart onclose={hidecarthandler}/>}
        <MainNavigation onshowcart ={showcarthandler}/>

<main><Suspense fallback={<div className='center'><LoadingSpinner/></div>}>{routes}</Suspense></main>
    </>
  )
}

export default App
