import React, { useCallback, useReducer, useState } from 'react'
import { useEffect } from 'react';
import {AuthContext}  from './auth-context';

// cart 

const defalutcartstate ={
  items:[],
  totalamount:0
}
const cartreducer=(state, action)=>{
  if(action.type === 'add'){
      const updatetotalamount = state.totalamount+ action.item.price *action.item.amount
      // console.log(updatetotalamount)

      const exestingcartitemindex= state.items.findIndex(
          (item) => item.id === action.item.id)
      const exestingcartitem= state.items[exestingcartitemindex];
      // let updateditem;
      let updateditems;
      if(exestingcartitem){
          const updateditem={
              ...exestingcartitem,
              amount: exestingcartitem.amount+ action.item.amount
          };
          updateditems= [...state.items]
          updateditems[exestingcartitemindex]= updateditem
      }else{
          // updateditem={...action.item}
          updateditems=state.items.concat(action.item)
      }
      
      // const updateitems= state.items.concat(action.item)
      // console.log(updateitems)
      return{            
      items:updateditems,
      totalamount:updatetotalamount
      }        
  }

  if(action.type === 'remove'){
      const exestingcartitemindex= state.items.findIndex(
          (item) => item.id === action.id)
      const exestingitem= state.items[exestingcartitemindex]
      const updatedtotalitem=state.totalamount- exestingitem.price;
      let updateditems;
      if(exestingitem.amount ===1){
          updateditems = state.items.filter(item => item.id !== action.id)
      }else{
          const updateditem ={...exestingitem, amount:exestingitem.amount -1}
          updateditems=[...state.items]
          updateditems[exestingcartitemindex]=updateditem
      }
      return{            
          items:updateditems,
          totalamount:updatedtotalitem
          }    
  }
      

  return defalutcartstate
}
