// const getlocalstorage = ()=>{
//   let storedData = localStorage.getItem('userData');
//   // console.log(storedData)
//   if(storedData){
//     return JSON.parse(storedData)
//   }else{
//     return[]
//   }
// }

const ContextProvider = (props) => {
  
    // const [userisLoggedIn, userIsLoggedIn] = useState(false);
    const[token, setToken] = useState(false)
    const[userId, setUserId]= useState(false);
    const[userName, setUserName]= useState(false);

    // const [customerisLoggedIn, setcustomerIsLoggedIn] = useState(false);
    const[tokenc, setTokenc] = useState(false)
    const[customerId, setcustomerId]= useState(false);
    const[customerName, setcustomerName]= useState(false);
    
    


    // cart 
    const[cartstate, dispatchacartaction]= useReducer(cartreducer, defalutcartstate)

    const additemtocarthandler=(item)=>{
        dispatchacartaction({type:'add', item:item})
    }

    const removeitemfromcarthandler=(id)=>{
        dispatchacartaction({type:'remove', id:id})
    }

//
    

    const login = useCallback((uid, nid, token, expirationDate) => {
      // setIsLoggedIn(true);
      setUserId(uid);
      setToken(token);
      setUserName(nid);

      const tokenExpirationDate= expirationDate || new Date(new Date().getTime() + 1000*60*60)
      localStorage.setItem('userData', JSON.stringify({ userId: uid, token: token, userName:nid, expiration: tokenExpirationDate.toISOString()}));
    }, []);

    
    
    
    const customerlogin= useCallback((cid, cnid, tokenc, expirationDate)=>{
      // setcustomerIsLoggedIn(true)      
      setTokenc(tokenc);
      setcustomerId(cid)
      setcustomerName(cnid)
      
      const tokenExpirationDate= expirationDate || new Date(new Date().getTime() + 1000*60*60)
      localStorage.setItem('customerData', JSON.stringify({ customerId: cid, tokenc: tokenc, customerName:cnid, expiration: tokenExpirationDate.toISOString() }));
    },[])
  

    // const finalorderlogin= useCallback(( fo)=>{
    //   setfinalIsLoggedIn(true)
    //   setfinalorderId(fo)
    // },[])
  

    const logout = useCallback(() => {
      // setIsLoggedIn(false);
      setToken(null)
      setTokenc(null)
      setUserId(null)
      setUserName(null)

      // setcustomerIsLoggedIn(false)
      setcustomerId(null)
      setcustomerName(null)

      localStorage.removeItem('userData')
      localStorage.removeItem('customerData')
    }, []);
  
    
    
    const delet = useCallback(() => {
      // setIsLoggedIn(false);
      setToken(null)
      setTokenc(null)
      setUserId(null)

      // setcustomerIsLoggedIn(false)
      setcustomerId(null)
    }, []);



    useEffect(() => {
      const storedData = JSON.parse(localStorage.getItem('userData'));
      console.log(storedData)
      if(storedData && storedData.token && new Date(storedData.expiration) > new Date()){
        login(storedData.userId,  storedData.userName, storedData.token, new Date(storedData.expiration))
      };
    }, [login]);


    useEffect(() => {
      const storedData = JSON.parse(localStorage.getItem('customerData'));
      console.log(storedData)
      if(storedData && storedData.tokenc && new Date(storedData.expiration) > new Date()){
        customerlogin(storedData.customerId, storedData.customerName, storedData.tokenc, new Date(storedData.expiration))
      };
    }, [customerlogin]);

    

    
  return (
    <>
       <AuthContext.Provider 
        value={ {
        isLoggedIn:!!token,
        token:token, 
        login: login,
        delet:delet, 
        userId:userId, 
        userName:userName,         
        logout: logout, 
        customerlogin,
        customerId,
        customerName,
        customerisLoggedIn: !!tokenc,
        // finalorderId,
        // finalorderisLoggedIn,
        // finalorderlogin,

        // cart         
        items:cartstate.items,
        totalamount:cartstate.totalamount,
        additem: additemtocarthandler,
        removeitem: removeitemfromcarthandler
        //
        }}>
            {props.children}
        </AuthContext.Provider>
    </>
  )
}

export default ContextProvider
