import React, { useState } from 'react';
import { Link, NavLink } from 'react-router-dom';

import MainHeader from './MainHeader';
import NavLinks from './NavLinks';
import SideDrawer from './SideDrawer';
import Backdrop from '../UIElements/Backdrop';
import './MainNavigation.css';
import Headercartbutton from '../../../cart/component/Layout/Headercartbutton';
// import { useCallback } from 'react';
import { useContext } from 'react';
import { AuthContext } from '../../context/auth-context';
// import Topnav from './Topnav/Topnav';

const MainNavigation = props => {
  const [drawerIsOpen, setDrawerIsOpen] = useState(false);

  const auth= useContext(AuthContext)

  const openDrawerHandler = () => {
    setDrawerIsOpen(true);
  };

  const closeDrawerHandler = () => {
    setDrawerIsOpen(false);
  };

  return (
    <React.Fragment>
    

      {drawerIsOpen && <Backdrop onClick={closeDrawerHandler} />}
      <SideDrawer show={drawerIsOpen} onClick={closeDrawerHandler}>
        <nav className="main-navigation__drawer-nav">
          <NavLinks onshowcart={props.onshowcart}/>
        </nav>
      </SideDrawer>



      <MainHeader>
        <button
          className="main-navigation__menu-btn"
          onClick={openDrawerHandler}
        >
          <span />
          <span />
          <span />
        </button>
        <h1 className="main-navigation__title">
          <Link to="/">G K A R T</Link>
        </h1>
        <nav className="main-navigation__header-nav" >
          <NavLinks onshowcart={props.onshowcart}/>
        </nav>
        
      {/* cart  */}
<div  className='cart1'>
{!auth.isLoggedIn  &&      
      <NavLink style={{textDecoration:"none", border:"none", marginLeft:"60px" }}  to={`/${auth.customerId}`} >
      <Headercartbutton onclick={props.onshowcart}  />
      </NavLink>
      } 
      </div>
      
      </MainHeader>
    </React.Fragment>
  );
};

export default MainNavigation;
